	import {mapState} from 'vuex'
	import {userlogin} from '../api.js'
	export default {
		data() {
			return {
				title: '',
				account:'',
				password:'',
				oldAccount:'',
				oldPassword:''
			}
		},
		onLoad() {
			try {
                const account = uni.getStorageSync('account');
				const password = uni.getStorageSync('password');
				const logout = uni.getStorageSync('logout');
				this.account=account;
				this.password=password;
                if (!logout&&password) {
					const data={
						username:account,
						password:password
					};
					userlogin(data).then(res=>{
						console.log(res.data)
						try {
								uni.setStorageSync('logout', 0);
						    } catch (e) {
						        // error
						    }
						if(res.data.status==0){
							uni.showToast({
								title: '登录成功',
								icon:'none'
							});
							uni.redirectTo({
								url:'index?code='+res.data.org_code+'&name='+res.data.userkey,
								
							})
						}
					})
                }
            } catch (e) {
                // error
            }
			

		},
		methods: {
			bindLogin(){
				
				const data={
					username:this.account,
					password:this.password
				};
				console.log(data);
				if(this.account.length==0){
					uni.showToast({
						title:'请输入账号',
						icon:'none'
					});
					return;
				}
				if(this.password.length==0){
					uni.showToast({
						title: '请输入密码',
						icon:'none'
					});
					return;
				}
				var that=this;
				userlogin(data).then(res=>{
					console.log(res.data)
					if(res.data.status==0){
						try{
							uni.setStorageSync('account', that.account);
							uni.setStorageSync('password',that.password);
							uni.setStorageSync('logout',0);
						}catch(e){
							
						}
						uni.redirectTo({
							url:'index?code='+res.data.org_code+'&name='+res.data.userkey,
							
						})
					}
				})
				
			}
		}
	}
